.key-row {
  display: grid;
  justify-content: center;
  gap: 3px; 
  width: 100%;
  max-width: 500px;
  margin-bottom: 3px;
}

.key-row.row-1 {
  grid-template-columns: repeat(10, 1fr); 
}

.key-row.row-2 {
  grid-template-columns: 0.5fr repeat(9, 1fr) 0.5fr; 
}

.key-row.row-3 {
  grid-template-columns: 1.5fr repeat(7, 1fr) 1.5fr; 
}

.key-box {
  background-color: rgb(92, 92, 92);
  width: 100%;
  height: 50px;
  font-size: 1.3rem;
  font-weight: bold;
  display: flex;
  align-items: center;
  justify-content: center;
  margin: 0;
  padding: 0;
  border-radius: 10% 10% 10% 10%;
  user-select: none;
}
.key-box:active {
  background-color: rgb(122, 122, 122);
}
.key-box > p {
  user-select: none;
}
.longer {
  width: 40px;
}
.keyboard-grid {
  background-color: rgb(18, 18, 19);
  position: fixed;
  bottom: 0;
  left: 50%;
  transform: translateX(-50%);
  width: 100%;
  max-width: 500px;
  max-height: 191px;
  z-index: 999;
  margin-top: 10px;
  margin-bottom: 10px;
  padding: 0 12px; 
  box-sizing: border-box; 
}

.key-letter, .react-icon {
  color: #ffffff;
  margin: 0;
  padding: 0;
  text-align: center;
}