.letter-box {
  width: 57px;
  height: 57px;
  font-size: 1.75rem;
  font-weight: bold;
  display: flex;
  align-items: center;
  justify-content: center;
  margin: 3px;
  transition: transform 0.5s linear, background-color 0.5s ease-in-out, width 0.5s linear;
  transform-style: preserve-3d;
  border-radius: 10% 10% 10% 10%;
}
.empty-letter {
  background-color: rgb(58, 58, 60);
}
.guessed-letter {
  background-color: rgb(83, 141, 78);
}
.half-letter {
  background-color: rgb(181, 159, 59);
}

.letter-flip {
  transform: rotateX(360deg);
}

.letter {
  backface-visibility: hidden;
}

